@-webkit-keyframes hvr-bob {
0% {
    -webkit-transform: translateY(-8px) skew(-2deg, 2deg);
            transform: translateY(-8px) skew(-2deg, 2deg);
}
50% {
    -webkit-transform: translateY(-4px) skew(-2deg, 2deg);
            transform: translateY(-4px) skew(-2deg, 2deg);
}
100% {
    -webkit-transform: skew(-2deg, 2deg) translateY(-8px);
            transform: skew(-2deg, 2deg) translateY(-8px);
}
}
@keyframes hvr-bob {
0% {
    -webkit-transform: translateY(-8px) skew(-2deg, 2deg);
            transform: translateY(-8px) skew(-2deg, 2deg);
}
50% {
    -webkit-transform: translateY(-4px) skew(-2deg, 2deg);
            transform: translateY(-4px) skew(-2deg, 2deg);
}
100% {
    -webkit-transform: skew(-2deg, 2deg) translateY(-8px);
            transform: skew(-2deg, 2deg) translateY(-8px);
}
}
.hvr-bob {
  -webkit-animation: hvr-bob 2s 0.3s ease-in-out infinite;
          animation: hvr-bob 2s 0.3s ease-in-out infinite;
  text-shadow: 0px 0px 10px black;
